   */

.MainNavigation_header__i4v2g li a::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 3px;
  top: 100%;
  left: 0;
  background: #e8e9eb;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: right;
          transform-origin: right;
}

.MainNavigation_header__i4v2g li a {
  position: relative;

}

.MainNavigation_header__i4v2g li a:hover {
  color: #95a5a6;
}

.MainNavigation_header__i4v2g li a:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: left;
          transform-origin: left;
}

.MainNavigation_header__i4v2g a {
  text-decoration: none;
  font-size: 1.5rem;
  color: white;
}

.MainNavigation_header__i4v2g a:hover,
